<!--
  You can get the Nth character, or letter, from a string by writing "string"[N].
  The returned value will be a string containing only one character (for example, "b").
  The first character has position zero, which causes the last one to be found at position string.length - 1. 
  In other words, a two-character string has length 2, and its characters have positions 0 and 1.
  Write a function countBs that takes a string as its only argument and returns a number that 
  indicates how many uppercase “B” characters there are in the string.
  Next, write a function called countChar that behaves like countBs, 
  except it takes a second argument that indicates the character that is to be counted
  (rather than counting only uppercase “B” characters). Rewrite countBs to make use of this new function.
-->

<script>
let countB = (s, c) => {
  let charToCompare = String(c);
  let found = [];

  for (let i = 0; i < s.length; i++) {
    let char = s[i];

    if (char === charToCompare) {
      found.push(char);
    }
  }
  console.log('===show me the char', found.length);
}

console.log(countB('BbccccbbBBBbb bb cccc bB bbCCbb bbb BBB', 'c'));
</script>



<p>Bean Counting</p>


